import { PostSelector } from '@frontgb/components'
import { BaseControl } from '@wordpress/components'
import { i18n } from 'frontgb'

import { __ } from '@wordpress/i18n'
import { Component } from '@wordpress/element'
import { RangeControl, SelectControl, CheckboxControl } from '@wordpress/components'
import { addQueryArgs } from '@wordpress/url'
import apiFetch from '@wordpress/api-fetch'


/**
 * ShortcodeAtts Component
 */
class ShortcodeAtts extends Component {
    /**
     * Constructor for PostAtts Component.
     * Sets up state, and creates bindings for functions.
     * @param object props - current component properties.
     */
    constructor(props) {
        super(...arguments);
        this.props = props;

        this.state = {
            categoriesList: [],
            CATEGORIES_LIST_QUERY: {
                per_page: -1,
            },
            selectedCat: [],
        }
    }

    componentWillMount() {
        const { CATEGORIES_LIST_QUERY } = this.state
        const { catRestBase, attributes } = this.props
        const { shortcode_atts } = attributes
        const { category } = shortcode_atts
        const restBase = catRestBase ? catRestBase : 'categories'
        this.isStillMounted = true
        this.fetchRequest = apiFetch( {
            path: addQueryArgs( `/wc/v3/products/${restBase}`, { ...CATEGORIES_LIST_QUERY, ...this.props.catArgs } ),
        } ).then(
            categoriesList => {
                if ( this.isStillMounted ) {
                    this.setState( { categoriesList } )
                }
            }
        ).catch(
            () => {
                if ( this.isStillMounted ) {
                    this.setState( { categoriesList: [] } )
                }
            }
        )

        if( category ) {
            this.setState( { selectedCat: category.split(',') } )
        }
    }

    componentWillUnmount() {
        this.isStillMounted = false
    }

    render() {
        const { attributes, postType, catRestBase, hideFields, setAttributes, minColumns, maxColumns } = this.props
        const { shortcode_tag, posts, shortcode_atts } = attributes
        const { limit, columns, orderby, order, category } = shortcode_atts
        const { categoriesList, selectedCat } = this.state

        console.log( shortcode_atts );
        console.log( selectedCat );

        return (
            <div>
                { !( hideFields && hideFields.includes('limit') ) && (
                <RangeControl
                    label={__('Limit', i18n)}
                    value={ limit }
                    onChange={newValue => {
                        setAttributes( { shortcode_atts: { ...shortcode_atts, limit: newValue } } )
                    }}
                    min={ 1 }
                    max={ 10 }
                />
                ) }
                { !( hideFields && hideFields.includes('columns') ) && (
                <RangeControl
                    label={__('Columns', i18n)}
                    value={ columns }
                    onChange={newValue => {
                        setAttributes( { shortcode_atts: { ...shortcode_atts, columns: newValue } } )
                    }}
                    min={ minColumns ? minColumns : 1 }
                    max={ maxColumns ? maxColumns : 8 }
                />
                ) }
                { !( hideFields && hideFields.includes('orderby') ) && (
                <SelectControl
                    label={__('Orderby', i18n)}
                    value={ orderby }
                    options={ [
                        { label: __('Title', i18n), value: 'title' },
                        { label: __('Date', i18n), value: 'date' },
                        { label: __('ID', i18n), value: 'id' },
                        { label: __('Random', i18n), value: 'rand' },
                    ] }
                    onChange={newValue => {
                        setAttributes( { shortcode_atts: { ...shortcode_atts, orderby: newValue } } );
                    }}
                />
                ) }
                { !( hideFields && hideFields.includes('order') ) && (
                <SelectControl
                    label={__('Order', i18n)}
                    value={ order }
                    options={ [
                        { label: __('ASC', i18n), value: 'ASC' },
                        { label: __('DESC', i18n), value: 'DESC' },
                    ] }
                    onChange={newValue => {
                        setAttributes( { shortcode_atts: { ...shortcode_atts, order: newValue } } );
                    }}

                />
                ) }
                { !( hideFields && hideFields.includes('shortcode_tag') ) && (
                <SelectControl
                    label={__('Shortcode Tags', i18n)}
                    value={ shortcode_tag }
                    options={ [
                        { label: __('Recent Products', i18n), value: 'recent_products' },
                        { label: __('Best Selling Products', i18n), value: 'best_selling_products' },
                        { label: __('Featured Products', i18n), value: 'featured_products' },
                        { label: __('Sale Products', i18n), value: 'sale_products' },
                        { label: __('Top Rated Products', i18n), value: 'top_rated_products' },
                        { label: __('Products', i18n), value: 'products' },
                        { label: __('Categories', i18n), value: 'product_category' },
                    ] }
                    onChange={newValue => {
                        setAttributes({ shortcode_tag: newValue });
                    }}
                />
                ) }
                { ( shortcode_tag === 'products' ) && (
                <BaseControl label={ __( 'Post Selector', i18n ) }>
                    <PostSelector
                      onPostSelect={post => {
                        attributes.posts.push(post);
                        let ids = attributes.posts.map( ( post, i ) => {
                            return post.id
                        } ).join(',')
                        setAttributes({ posts: [...attributes.posts], shortcode_atts: { ...shortcode_atts, ids: ids } });
                      }}
                      posts={attributes.posts ? attributes.posts : [] }
                      onChange={newValue => {
                        let ids = attributes.posts.map( ( post, i ) => {
                            return post.id
                        } ).join(',')
                        setAttributes({ posts: [...newValue], shortcode_atts: { ...shortcode_atts, ids: ids } });
                      }}
                      postType={'product'}
                    />
                </BaseControl>
                ) }
                { ( shortcode_tag === 'product_category' ) && (
                <SelectControl
                    label={ __( 'Choose Category', i18n ) }
                    value={ selectedCat }
                    options={ categoriesList.map( ( { slug, name } ) => ( {
                        value: slug,
                        label: name,
                    } ) ) }
                    onChange={newValue => {
                        this.setState( { selectedCat: newValue } )
                        setAttributes( { shortcode_atts: { ...shortcode_atts, category: newValue.join(',') } } );
                    }}
                    multiple
                />
                ) }
            </div>
        );
    }
}

export default ShortcodeAtts