import { __ } from '@wordpress/i18n'
import { registerPlugin } from '@wordpress/plugins'
import { PluginPostStatusInfo, PluginSidebarMoreMenuItem, PluginSidebar } from '@wordpress/edit-post'
import { FrontGBIcon } from '@frontgb/icons'
import { i18n } from 'frontgb'

import DocsPluginPostStatusInfo from './meta-fields/docs'
import PagePluginSidebar from './meta-fields/page'
import JetpackTestimonialPluginSidebar from './meta-fields/jetpack-testimonial'
import CustomerStoryPluginSidebar from './meta-fields/customer-story'

const PluginCustomMetaFields = () => {
    return(
        <>
            <PluginPostStatusInfo>
                <DocsPluginPostStatusInfo />
            </PluginPostStatusInfo>
            <PluginSidebarMoreMenuItem
					target="front-options-sidebar"
					icon={ FrontGBIcon }
            >
					{ __( 'Front Options', i18n ) }
			</PluginSidebarMoreMenuItem>
            <PluginSidebar name="front-options-sidebar" title={ __( 'Front Options', i18n ) }>
                <PagePluginSidebar />
                <JetpackTestimonialPluginSidebar />
                <CustomerStoryPluginSidebar />
            </PluginSidebar>
        </>
    )
}

registerPlugin( 'fgb-sidebar', {
    icon: FrontGBIcon,
    render: PluginCustomMetaFields
})
