import { __ } from '@wordpress/i18n'
import { i18n } from 'frontgb'
import classnames from 'classnames'
import { RichText } from '@wordpress/block-editor'
import { SVGInjector } from '@frontgb/components'
import { getIconClassName, getIconPath } from '@frontgb/components/icon-control'

const ButtonEdit = props => {
	const {
		className = '',
		align = 'center',
		background = 'primary',
		design = 'default',
		size = 'default',
		isBtn = true,
		isWide = false,
		isWideSM = false,
		isDisable = false,
		isBlock = false,
		borderRadius = 'default',
		isTransition = true,
		text = '',
		icon = null,
		isIconAfterText = false,
		isIconButton = false,
		isSelected = null,
		dataAtts = {},
		onClick = () => {},
		onChange = () => {},
	} = props

	const mainClasses = classnames( [
		className,
		'fgb-button',
	], {
		'btn': isBtn,
		[ `fgb-button--align-${ align }` ]: align,
		[`btn-${ background }`]: design === 'default',
		[`btn-${ design }-${ background }`]: design !== 'default',
		[`${ size }`]: size !== 'default',
		'fgb-icon': ( icon && icon.startsWith("fgb") ),
		'btn-icon': isIconButton,
		'btn-wide': isWide,
		'btn-sm-wide': isWideSM,
		'disabled btn-white': isDisable,
		'btn-block': isBlock,
		'transition-3d-hover': isTransition,
		[`${ borderRadius }`]: borderRadius !== 'default'
	} )

	let iconContent = false;

	if( icon && icon.startsWith("fgb") ) {
		const iconClasses = classnames( [ 
			'ie-height-20', 'max-width-4', 'button-width'
		], {
			'btn-icon__inner': isIconButton,
			'mr-sm-1': !isIconButton && !isIconAfterText,
			'ml-sm-1': !isIconButton && isIconAfterText
		} )
		const buttonIconPath = getIconPath( icon )

		iconContent = (
			<SVGInjector
				classNameFig={ iconClasses }
				className="js-svg-injector"
				src={ buttonIconPath }
				alt="SVG"
			/>
		)
	} else {
		const buttonIconClass = getIconClassName( icon )
		const iconClasses = classnames( [ 
			buttonIconClass
		], {
			'btn-icon__inner': isIconButton,
			'mr-sm-1': !isIconButton && !isIconAfterText,
			'ml-sm-1': !isIconButton && isIconAfterText
		} )

		iconContent = <span className={ iconClasses }></span>
	}

	return (
		<span
			className={ isBlock && !isIconButton ? 'd-block' : 'd-inline-block' }
			onClick={ onClick }
			data-is-placeholder-visible={ RichText.isEmpty( text ) }
		>
			{ /* eslint-disable-next-line jsx-a11y/anchor-is-valid */ }
			<a
				href="javascript:;"
				className={ mainClasses }
				{ ...dataAtts }
			>
				{ icon && ! isIconAfterText && iconContent }
				{
					// Should be tagName="span", but div for now because of issue
					// @see https://github.com/WordPress/gutenberg/issues/7311
				}
				{ ! isIconButton &&
					<RichText
						tagName="div"
						className={ 'fgb-button--inner' }
						placeholder={ __( 'Button text', i18n ) }
						value={ text }
						onChange={ onChange }
						keepPlaceholderOnFocus
					/>
				}

				{ icon && isIconAfterText && iconContent }
			</a>
		</span>
	)
}

ButtonEdit.Content = props => {
	const {
		className = '',
		align = 'center',
		background = 'primary',
		design = 'default',
		size = 'default',
		isBtn = true,
		isWide = false,
		isWideSM = false,
		isDisable = false,
		isBlock = false,
		borderRadius = 'default',
		isTransition = true,
		url = '',
		newTab = false,
		text = '',
		icon = null,
		isIconAfterText = false,
		isIconButton = false,
		dataAtts = {},
	} = props

	const mainClasses = classnames( [
		className,
		'fgb-button',
	], {
		'btn': isBtn,
		[ `fgb-button--align-${ align }` ]: align,
		[`btn-${ background }`]: design === 'default',
		[`btn-${ design }-${ background }`]: design !== 'default',
		[`${ size }`]: size !== 'default',
		'fgb-icon': ( icon && icon.startsWith("fgb") ),
		'btn-icon': isIconButton,
		'btn-wide': isWide,
		'btn-sm-wide': isWideSM,
		'disabled btn-white': isDisable,
		'btn-block': isBlock,
		'transition-3d-hover': isTransition,
		[`${ borderRadius }`]: borderRadius !== 'default'
	} )

	let iconContent = false;

	if( icon && icon.startsWith("fgb") ) {
		const iconClasses = classnames( [ 
			'ie-height-20', 'max-width-4', 'button-width'
		], {
			'btn-icon__inner': isIconButton,
			'mr-sm-1': !isIconButton && !isIconAfterText,
			'ml-sm-1': !isIconButton && isIconAfterText
		} )
		const buttonIconPath = getIconPath( icon )

		iconContent = (
			<SVGInjector
				classNameFig={ iconClasses }
				className="js-svg-injector"
				src={ buttonIconPath }
				alt="SVG"
			/>
		)
	} else {
		const buttonIconClass = getIconClassName( icon )
		const iconClasses = classnames( [ 
			buttonIconClass
		], {
			'btn-icon__inner': isIconButton,
			'mr-sm-1': !isIconButton && !isIconAfterText,
			'ml-sm-1': !isIconButton && isIconAfterText
		} )

		iconContent = <span className={ iconClasses }></span>
	}

	return (
		<a
			href={ url }
			className={ mainClasses }
			target={ newTab ? '_blank' : undefined }
			rel={ newTab ? 'noopener noreferrer' : undefined }
			{ ...dataAtts }
		>
			{ icon && ! isIconAfterText && iconContent }
			{ ! isIconButton &&
				<RichText.Content
					tagName="div"
					className={ 'fgb-button--inner' }
					value={ text }
				/>
			}
			{ icon && isIconAfterText && iconContent }
		</a>
	)
}

export default ButtonEdit