import { BaseControl, Button, ButtonGroup, RangeControl, ToggleControl, SelectControl, RadioControl} from '@wordpress/components'
import { ColorPaletteControl, PanelColorSettings } from '@frontgb/components'
import { __ } from '@wordpress/i18n'
import { applyFilters } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import ImageControl from '../image-control'
import { i18n } from 'frontgb'

function OverlaySettings( props ) {
    const {
        backgroundColor,
        onChangeBackgroundColor,
        onChangeBackground,
        onRemoveBackground,
        backgroundImageURL,
        onChangeBackgroundImage,
        onRemoveBackgroundImage,
        onChangeBackgroundType,
    } = props

return (
        <div className = "frontgb-radiobutton-bg"> 
            <Fragment>
                <RadioControl
                    label="Overlay Color"
                    selected={ backgroundColor }
                    options={ applyFilters( 'frontgb.overlay-settings.edit.designs', [
                        {
                            label: __( 'Gradient Overlay  v1', i18n ), value: 'gradient-overlay-half-primary-v1',
                        },
                        {
                            label: __( 'Gradient Overlay  v2', i18n ), value: 'gradient-overlay-half-primary-v2',
                        },
                        {
                            label: __( 'Gradient Overlay  v3', i18n ), value: 'gradient-overlay-half-primary-v3',
                        },
                        {
                            label: __( 'Gradient Overlay  v4', i18n ), value: 'gradient-overlay-half-primary-v4',
                        },
                        {
                            label: __( 'Gradient Overlay  v5', i18n ), value: 'gradient-overlay-half-indigo-v1',
                        },
                        {
                            label: __( 'Gradient Overlay  v6', i18n ), value: 'gradient-overlay-half-info-v1',
                        },
                        {
                            label: __( 'Gradient Overlay v7', i18n ), value: 'gradient-overlay-half-dark-v1',
                        },
                        {
                            label: __( 'Gradient Overlay  v8', i18n ), value: 'gradient-overlay-half-dark-v2',
                        },
                        {
                            label: __( 'Gradient Overlay  v9', i18n ), value: 'gradient-overlay-half-primary-video-v1',
                        },
                    ] ) }
                    onChange={ onChangeBackgroundColor }
                />
                <div className="components-color-palette__custom-clear-wrapper clear-button">
                    <Button
                        className="components-color-palette__clear"
                        type="button"
                        onClick= { onRemoveBackground }
                        isSmall
                        isDefault
                    >
                        { __( 'Clear', i18n ) }
                    </Button>
                </div>
            </Fragment>
            { ( onChangeBackgroundImage && onChangeBackground === undefined ) &&
            <Fragment>
                <ImageControl
                    label={ __( 'Overlay Image', i18n ) }
                    help={ __( 'Use .mp4 format for videos', i18n ) }
                    onRemove={ media => {
                        if ( onChangeBackgroundType ) {
                            onChangeBackgroundType( '' )
                        }
                        onRemoveBackgroundImage( media )
                    } }
                    onChange={ media => {
                        if ( onChangeBackgroundType ) {
                            onChangeBackgroundType( media.url.match( /(mp4|webm|ogg)/i ) ? 'video' : '' )
                        }
                        onChangeBackgroundImage( media )
                    } }
                    imageURL={ backgroundImageURL }
                    allowedTypes={ onChangeBackgroundType ? [ 'image', 'video' ] : [ 'image' ] }
                />
        </Fragment>
        }
    </div>
    )
}

export default OverlaySettings