import { updateCategory } from '@wordpress/blocks'

export const colorizeIcon = SvgIcon => {
    return cloneElement( SvgIcon, {
        fill: 'url(#frontgb-gradient)',
        className: 'ugb-frontgb-icon-gradient',
    } )
}

// Add an icon to our block category.
if ( typeof window.wp.blocks !== 'undefined' && typeof window.wp.blocks.updateCategory !== 'undefined' ) {
    updateCategory( 'frontgb', {
        icon: colorizeIcon( <SVGFrontGBIcon className="components-panel__icon" width="20" height="20" /> ),
    } )

    updateCategory( 'frontgb-products', {
        icon: colorizeIcon( <SVGFrontGBIcon className="components-panel__icon" width="20" height="20" /> ),
    } )

    updateCategory( 'frontgb-portfolio', {
        icon: colorizeIcon( <SVGFrontGBIcon className="components-panel__icon" width="20" height="20" /> ),
    } )

	updateCategory( 'frontgb-hero', {
		icon: colorizeIcon( <SVGFrontGBIcon className="components-panel__icon" width="20" height="20" /> ),
	} )

    updateCategory( 'frontgb-info-blocks', {
        icon: colorizeIcon( <SVGFrontGBIcon className="components-panel__icon" width="20" height="20" /> ),
    } )

    updateCategory( 'frontgb-jobs', {
        icon: colorizeIcon( <SVGFrontGBIcon className="components-panel__icon" width="20" height="20" /> ),
    } )

    updateCategory( 'frontgb-hero', {
        icon: colorizeIcon( <SVGFrontGBIcon className="components-panel__icon" width="20" height="20" /> ),
    } )

    updateCategory( 'frontgb-hero-carousel', {
        icon: colorizeIcon( <SVGFrontGBIcon className="components-panel__icon" width="20" height="20" /> ),
    } )

    updateCategory( 'frontgb-hero-form', {
        icon: colorizeIcon( <SVGFrontGBIcon className="components-panel__icon" width="20" height="20" /> ),
    } )

    updateCategory( 'frontgb-pricing', {
        icon: colorizeIcon( <SVGFrontGBIcon className="components-panel__icon" width="20" height="20" /> ),
    } )

    updateCategory( 'frontgb-gallery', {
        icon: colorizeIcon( <SVGFrontGBIcon className="components-panel__icon" width="20" height="20" /> ),
    } )

    updateCategory( 'frontgb-contact-form', {
        icon: colorizeIcon( <SVGFrontGBIcon className="components-panel__icon" width="20" height="20" /> ),
    } )

    updateCategory( 'frontgb-docs', {
        icon: colorizeIcon( <SVGFrontGBIcon className="components-panel__icon" width="20" height="20" /> ),
    } )


    updateCategory( 'frontgb-footer', {
        icon: colorizeIcon( <SVGFrontGBIcon className="components-panel__icon" width="20" height="20" /> ),
    } )

    updateCategory( 'frontgb-stats', {
        icon: colorizeIcon( <SVGFrontGBIcon className="components-panel__icon" width="20" height="20" /> ),
    } )

}

// Add our SVG gradient placeholder definition that we'll reuse.
domReady( () => {
    const frontgbGradient = document.createElement( 'DIV' )
    document.querySelector( 'body' ).appendChild( frontgbGradient )
    render(
        <svg
            xmlns="http://www.w3.org/2000/svg"
            className="ugb-frontgb-gradient"
            height="0"
            width="0"
            style={ { opacity: 0 } }
        >
            <defs>
                <linearGradient id="frontgb-gradient">
                    <stop offset="0%" stopColor="#3F7DE0" stopOpacity="1" />
                    <stop offset="100%" stopColor="#377DFF" stopOpacity="1" />
                </linearGradient>
            </defs>
        </svg>,
        frontgbGradient
    )
} )

