/**
 * Slick Control with Responsive Options
 */
import { __ } from '@wordpress/i18n'
import { PanelBody, ToggleControl } from '@wordpress/components'
import { ColumnControl } from '@frontgb/components'
import { i18n } from 'frontgb'

const SlickControl = props => {
    const {
        onChangeInfinite,
        onChangeAutoplay,
        onChangeArrows,
        onChangeCentermode,
        onChangeDots,
        infinite = undefined,
        autoplay = undefined,
        arrows = undefined,
        dots = undefined,
        centermode = undefined,
        dotsInner,
        onChangeDotsinner,
        onChangexs,
        onChangesm,
        onChangemd,
        onChangelg,
        onChangexl,
        xsColumns = undefined,
        smColumns = undefined,
        mdColumns = undefined,
        lgColumns = undefined,
        xlColumns = undefined,
        xsMin = 1,
        xsMax = 2,
        smMin = 1,
        smMax = 4,
        mdMin = 1,
        mdMax = 6,
        lgMin = 1,
        lgMax = 8,
        xlMin = 1,
        xlMax = 8
    } = props

    return (
        <PanelBody title={ __( 'Slick Settings', i18n ) }>
            <ColumnControl { ...props } />
            { onChangeInfinite && (
                <ToggleControl
                    label={ __( 'Infinite', i18n ) }
                    checked={ infinite }
                    onChange={ onChangeInfinite }
                />
            ) }
            { onChangeAutoplay && (
                <ToggleControl
                    label={ __( 'Autoplay', i18n ) }
                    checked={ autoplay }
                    onChange={ onChangeAutoplay }
                />
            ) }
            { onChangeArrows && (
                <ToggleControl
                    label={ __( 'Arrows', i18n ) }
                    checked={ arrows }
                    onChange={ onChangeArrows }
                />
            ) }
            { onChangeCentermode && (
                <ToggleControl
                    label={ __( 'Centermode', i18n ) }
                    checked={ centermode }
                    onChange={ onChangeCentermode }
                />
            ) }
            { onChangeDots && (
                <ToggleControl
                    label={ __( 'Dots', i18n ) }
                    checked={ dots }
                    onChange={ onChangeDots }
                />
            ) }
            { ( onChangeDotsinner && dots ) && (
                <ToggleControl
                    label={ __( 'Dots Inner', i18n ) }
                    checked={ dotsInner }
                    onChange={ onChangeDotsinner }
                />
            ) }
        </PanelBody>
    )
}

export default SlickControl