import { __ } from '@wordpress/i18n'
import { compose } from '@wordpress/compose'
import { withSelect, withDispatch } from '@wordpress/data'
import { PanelBody, SelectControl, TextControl } from '@wordpress/components'
import { PostTypeCheck } from '@frontgb/components'
import { Fragment } from '@wordpress/element'
import { i18n } from 'frontgb'

let JetpackTestimonialPluginSidebar = (props) => {
    const { rating, setRating, authorPosition, setAuthorPosition } = props
    return(
        <Fragment>
            <PostTypeCheck name="jetpack-testimonial">
                <PanelBody title={ __( 'General', i18n ) } initialOpen={ false }>
                   <SelectControl
                        label={ __( 'Star Rating', i18n ) }
                        value={ rating }
                        options={ [
                            { label: __( '1 Star', i18n ), value: 1 },
                            { label: __( '2 Star', i18n ), value: 2 },
                            { label: __( '3 Star', i18n ), value: 3 },
                            { label: __( '4 Star', i18n ), value: 4 },
                            { label: __( '5 Star', i18n ), value: 5 },
                        ] }
                        onChange={ setRating }
                    />
                    <TextControl
                        label={ __( 'Author Position', i18n ) }
                        value={ authorPosition }
                        onChange={ setAuthorPosition }
                    />
                </PanelBody>
            </PostTypeCheck>
        </Fragment>
    )
}

export default compose(
    withSelect( ( select ) => {
        const { getEditedPostAttribute } = select( 'core/editor' )
        const meta = getEditedPostAttribute( 'meta' ) || {}
        const { _rating, _author_position } = meta
        return {
            rating: _rating,
            authorPosition: _author_position,
        }
    } ),
    withDispatch( ( dispatch ) => ( {
        setRating( value ) {
            dispatch( 'core/editor' ).editPost( { meta: { _rating: value } } )
        },
        setAuthorPosition( value ) {
            dispatch( 'core/editor' ).editPost( { meta: { _author_position: value } } )
        },
    } ) ),
)( JetpackTestimonialPluginSidebar )