import { DesignControl, IconControl, PanelColorSettings, BackgroundSettings, URLInputControl } from '@frontgb/components'
import { RangeControl, SelectControl,ToggleControl, BaseControl } from '@wordpress/components'
import { __ } from '@wordpress/i18n'
import { applyFilters } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import ImageDesignBasic from './images/basic.png'
import ImageDesignPlain from './images/plain.png'
import { i18n } from 'frontgb'

function PanelSocialNetworksSettings( props ) {
    const {
        iconBackground,
        iconCustomBackground,
        icon,
        socialNetworkLink,
        enableMargin,
        enableCircle,
        enableTransparent,
        onChangeMargin,
        socialNetworkIcon,
        onChangeIconBackground,
        onChangeSocialNetworkLink,
        onChangeTransparent,
        onChangeCircle,
        onChangeSocialNetworkIcon, // = () => {},
        onChangeIcon, // = () => {},
        onChangeIconCustomBackground,
        onRemoveIconBackground,
        socialnetworkdesign,
        onChangeSocialNetworkDesign,
        hideFields,
    } = props


    return (
        <Fragment>
            { onChangeSocialNetworkDesign &&
                <DesignControl
                    label={ __( 'Design', i18n ) }
                    selected={ socialnetworkdesign }
                    options={ [
                        { label: __( 'Default', i18n ), value: 'default', image: ImageDesignBasic },
                        { label: __( 'Soft', i18n ), value: 'soft', image: ImageDesignPlain },
                        ...applyFilters( 'frontgb.social-network.edit.designs', [] ),
                    ] }
                    onChange={ onChangeSocialNetworkDesign }
                />
            }
           { !( hideFields && hideFields.includes('icon') ) ? (
            <RangeControl
                label={ __( 'Icon', i18n ) }
                value={ icon }
                min="1"
                max="10"
                onChange={ onChangeIcon }
            />
            ) : '' }
            { !( hideFields && hideFields.includes('enableMargin') ) ? (
            <ToggleControl
                label={ __( 'Enable Margin', i18n ) }
                checked={ enableMargin }
                onChange={ onChangeMargin }
            />
            ) : '' }

            { !( hideFields && hideFields.includes('enableTransparent') ) ? (
            <ToggleControl
                label={ __( 'Enable Transparent', i18n ) }
                checked={ enableTransparent }
                onChange={ onChangeTransparent }
            />
            ) : '' }
            { !( hideFields && hideFields.includes('enableCircle') ) ? (
            <ToggleControl
                label={ __( 'Enable Circle', i18n ) }
                checked={ enableCircle }
                onChange={ onChangeCircle }
            />
            ) : '' }
            { !( hideFields && hideFields.includes('socialNetworkIcon') ) ? (
            <IconControl
                label={ __( 'Social Network Icon', i18n) }
                value={ socialNetworkIcon }
                onChange={ onChangeSocialNetworkIcon }
            />
            ) : '' }
            { !( hideFields && hideFields.includes('iconbackground') ) ? (
            <BackgroundSettings
                backgroundLabel = { __("Icon Background Color", i18n ) } 
                background={ iconBackground }
                prefix=''
                softBackground= { false }
                onChangeBackground={ onChangeIconBackground }
                onRemoveBackground={ onRemoveIconBackground }
            />
            ) : '' }
            { !( hideFields && hideFields.includes('socialNetworkLink') ) ? (
            <BaseControl label={ __( 'Social Network Link', i18n ) }>
                <URLInputControl
                    value={ socialNetworkLink }
                    onChange={ onChangeSocialNetworkLink }
                />
            </BaseControl>
            ) : '' }
        </Fragment>
    )
}

export default PanelSocialNetworksSettings