import { Component, renderToString } from '@wordpress/element'
import $ from 'jquery'

class SVGInjector extends Component {

    componentDidMount() {
        this.editorScriptInit()
    }

    componentDidUpdate( prevProps ) {
        if ( this.props.src !== prevProps.src || this.props.dataImgPaths !== prevProps.dataImgPaths ) {
            let imgTag = renderToString( this.renderIMG() )
            $(this.el).html( imgTag )

            this.editorScriptInit()
        }
    }

    editorScriptInit() {
        let element = $(this.el).find('.js-svg-injector')
        if( $(element).length ) {
            $.HSCore.components.HSSVGIngector.init(element)
        }
    }

    renderIMG() {
        const {
            className = 'js-svg-injector',
            alt = '',
            src = undefined,
            dataParent = undefined,
            dataImgPaths = undefined,
        } = this.props

        let imgProps = {
            className: className,
            alt: alt
        }

        if( src ) {
            imgProps['src'] = src
        }

        if( dataImgPaths ) {
            imgProps['data-img-paths'] = dataImgPaths
        }

        if( dataParent ) {
            imgProps['data-parent'] = dataParent
        }

        return (
            <img { ...imgProps } />
        )
    }

    render() {
        const {
            classNameFig = '',
            idFig = undefined
        } = this.props

        let figProps = {
            className: classNameFig,
        }

        if( idFig ) {
            figProps['id'] = idFig
        }

        return (
            <figure { ...figProps } ref={ el => this.el = el }>{ this.renderIMG() }</figure>
        )
    }
}

export default SVGInjector