import { applyFilters } from '@wordpress/hooks'
import { range } from '@frontgb/util'
import { themeAssetsURL } from 'frontgb'

const themeIconsURL = themeAssetsURL + 'svg/icons/'

var iconsObj = {}

range( 1, 67 ).forEach( i => {
    iconsObj[ `fgbIcon${ i }` ] = {
        prefix: 'fgb',
        iconName: `icon-${ i }`,
        iconPath: `${ themeIconsURL }icon-${ i }.svg`
    }
    iconsObj[ `fgbIconWhite${ i }` ] = {
        prefix: 'fgb',
        iconName: `icon-white-${ i }`,
        iconPath: `${ themeIconsURL }icon-white-${ i }.svg`
    }
} )

export const fgb = applyFilters( 'frontgb.component.icon.control.fgb.icons', iconsObj );