import { __ } from '@wordpress/i18n'
import { Component } from '@wordpress/element'
import { TextControl, RangeControl, SelectControl, ToggleControl, BaseControl } from '@wordpress/components'
import { PostSelector } from '@frontgb/components'
import { addQueryArgs } from '@wordpress/url'
import apiFetch from '@wordpress/api-fetch'
import { i18n } from 'frontgb'

/**
 * PostAtts Component
 */
class PostAtts extends Component {
    /**
     * Constructor for PostAtts Component.
     * Sets up state, and creates bindings for functions.
     * @param object props - current component properties.
     */
    constructor(props) {
        super(...arguments);
        this.props = props;

        this.state = {
            categoriesList: [],
            CATEGORIES_LIST_QUERY: {
                per_page: -1,
            }
        }
    }

    componentWillMount() {
        const { CATEGORIES_LIST_QUERY } = this.state
        const { catRestBase } = this.props
        const restBase = catRestBase ? catRestBase : 'categories'
        this.isStillMounted = true
        this.fetchRequest = apiFetch( {
            path: addQueryArgs( `/wp/v2/${restBase}`, { ...CATEGORIES_LIST_QUERY, ...this.props.catArgs } ),
        } ).then(
            categoriesList => {
                if ( this.isStillMounted ) {
                    this.setState( { categoriesList } )
                }
            }
        ).catch(
            () => {
                if ( this.isStillMounted ) {
                    this.setState( { categoriesList: [] } )
                }
            }
        )
    }

    componentWillUnmount() {
        this.isStillMounted = false
    }

    /**
     * Renders the PostAtts component.
     */
    render() {
        const { attributes, postType, catRestBase, hideFields, setAttributes, minLimit, maxLimit, minColumns, maxColumns } = this.props;
        const { shortcode_atts, posts } = attributes;
        const { per_page, orderby, order, ids, categories, featured, columns } = shortcode_atts;
        const { categoriesList } = this.state

        return (
            <div>
                { !( hideFields && hideFields.includes('limit') ) && (
                <RangeControl
                    label={ __( 'Limit', i18n ) }
                    value={ per_page }
                    onChange={newValue => {
                        setAttributes( { shortcode_atts: { ...shortcode_atts, per_page: newValue } } )
                    }}
                    min={ minLimit ? minLimit : 1 }
                    max={ maxLimit ? maxLimit : 10 }
                />
                ) }
                { !( hideFields && hideFields.includes('columns') ) && (
                <RangeControl
                    label={ __( 'Columns', i18n ) }
                    value={ columns }
                    onChange={newValue => {
                        setAttributes( { shortcode_atts: { ...shortcode_atts, columns: newValue } } )
                    }}
                    min={ minColumns ? minColumns : 1 }
                    max={ maxColumns ? maxColumns : 4 }
                />
                ) }
                { !( hideFields && hideFields.includes('orderby') ) && (
                <SelectControl
                    label={ __( 'Orderby', i18n ) }
                    value={ orderby }
                    options={ [
                        { label: __( 'Title', i18n ), value: 'title' },
                        { label: __( 'Date', i18n ), value: 'date' },
                        { label: __( 'ID', i18n ), value: 'id' },
                        { label: __( 'Random', i18n ), value: 'rand' },
                    ] }
                    onChange={newValue => {
                        setAttributes( { shortcode_atts: { ...shortcode_atts, orderby: newValue } } );
                    }}
                />
                ) }
                { !( hideFields && hideFields.includes('order') ) && (
                <SelectControl
                    label={ __( 'Order', i18n ) }
                    value={ order }
                    options={ [
                        { label: __( 'ASC', i18n ), value: 'ASC' },
                        { label: __( 'DESC', i18n ), value: 'DESC' },
                    ] }
                    onChange={newValue => {
                        setAttributes( { shortcode_atts: { ...shortcode_atts, order: newValue } } );
                    }}
                />
                ) }
                { !( hideFields && hideFields.includes('ids') ) && (
                <BaseControl label={ __( 'Post Selector', i18n ) }>
                    <PostSelector
                        postType={postType}
                        onPostSelect={post => {
                            attributes.posts.push(post);
                            let ids = attributes.posts.map( ( post, i ) => {
                                return post.id
                            } ).join(',')
                            setAttributes({ posts: [...attributes.posts], shortcode_atts: { ...shortcode_atts, ids: ids } });
                        }}
                        posts={attributes.posts ? attributes.posts : [] }
                        onChange={newValue => {
                            let ids = attributes.posts.map( ( post, i ) => {
                                return post.id
                            } ).join(',')
                            setAttributes({ posts: [...newValue], shortcode_atts: { ...shortcode_atts, ids: ids } });
                        }}
                    />
                </BaseControl>
                ) }
                { !( hideFields && hideFields.includes('categories') ) && (
                <SelectControl
                    label={ __( 'Choose Category', i18n ) }
                    value={ categories }
                    options={ categoriesList.map( ( { slug, name } ) => ( {
                        value: slug,
                        label: name,
                    } ) ) }
                    onChange={newValue => {
                        setAttributes( { shortcode_atts: { ...shortcode_atts, categories: newValue } } );
                    }}
                    multiple
                />
                ) }
                { !( hideFields && hideFields.includes('featured') ) && (
                <ToggleControl
                    label={ __( 'Is Featured ? ', i18n ) }
                    checked={ featured }
                    onChange={newValue => {
                        setAttributes( { shortcode_atts: { ...shortcode_atts, featured: newValue ? newValue : undefined } } );
                    }}
                />
                ) }
            </div>
        );
    }
}
export default PostAtts