import { __ } from '@wordpress/i18n'
import classnames from 'classnames'

/**
 * Returns an array range of numbers.
 *
 * @param {number} start Starting number range.
 * @param {number} end Ending number range.
 *
 * @return {Array} The range of start to end.
 *
 * @see https://stackoverflow.com/questions/36947847/how-to-generate-range-of-numbers-from-0-to-n-in-es2015-only
 */
export const range = ( start, end ) => {
	return Array.from( { length: ( end - start ) }, ( v, k ) => k + start )
}

export const arrayChunk = ( arr, size = 3 ) => {
	let arrays = []

	while ( arr.length > 0 ) {
		arrays.push( arr.splice( 0, size ) )
	}

	return arrays
}

/**
 * From a URL, get the video ID and provider: YouTube or Vimeo.
 *
 * @param {string} url
 *
 * @return {Object} An object containing the video ID and provider name.
 */
export const getVideoProviderFromURL = url => {
	let id = ''

	// Check for YouTube.
	id = ( url.match( /youtube\.com\/watch\?v=([^\&\?\/]+)/i ) || [] )[ 1 ]

	if ( ! id ) {
		id = ( url.match( /youtube\.com\/embed\/([^\&\?\/]+)/i ) || [] )[ 1 ]
	}
	if ( ! id ) {
		id = ( url.match( /youtube\.com\/v\/([^\&\?\/]+)/i ) || [] )[ 1 ]
	}
	if ( ! id ) {
		id = ( url.match( /youtu\.be\/([^\&\?\/]+)/i ) || [] )[ 1 ]
	}

	if ( id ) {
		return {
			type: 'youtube',
			id,
		}
	}

	// Check for Vimeo.
	id = ( url.match( /vimeo\.com\/(\w*\/)*(\d+)/i ) || [] )[ 2 ]
	if ( ! id ) {
		id = ( url.match( /^\d+$/i ) || [] )[ 0 ]
	}

	if ( id ) {
		return {
			type: 'vimeo',
			id,
		}
	}

	return {
		type: 'youtube',
		id: url,
	}
}

/**
 * Generates a placeholder text for short to long descriptions.
 *
 * @param {string} length The length of the placeholder. Values are: short, medium, long, normal. Defaults to normal.
 *
 * @return {string} Placeholder string.
 */
export const descriptionPlaceholder = length => {
	if ( length === 'short' ) {
		return __( 'Description for this block. You can use this space for describing your block.' )
	} else if ( length === 'medium' ) {
		return `${ descriptionPlaceholder() } ${ descriptionPlaceholder( 'short' ) }`
	} else if ( length === 'long' ) {
		return `${ descriptionPlaceholder( 'medium' ) } ${ descriptionPlaceholder() } ${ descriptionPlaceholder( 'short' ) }`
	}
	return __( 'Description for this block. Use this space for describing your block. Any text will do.' )
}


/**
 * Generates a placeholder text for short to long descriptions.
 *
 * @param {string} length The length of the placeholder. Values are: short, medium, long, normal. Defaults to normal.
 *
 * @return {string} Placeholder string.
 */
export const actionPlaceholder = length => {
	if ( length === 'short' ) {
		return __( 'See Help Front in action.' )
	} else if ( length === 'long' ) {
		return `${ actionPlaceholder( 'medium' ) } ${ actionPlaceholder() } ${ actionPlaceholder( 'short' ) }`
	}
	return __( 'See Help Front in action.' )
}

/**
 * Are we inside the Gutenberg Block Editor?
 *
 * @return {boolean} True if inside the Gutenberg Block Editor, false if not (e.g. in the frontend).
 */
export const isEditor = () => typeof window.wp !== 'undefined' && typeof window.wp.editor !== 'undefined'

/**
 * Makes a unique ID based on the block's clientID.
 *
 * @param {string} clientID The block's clientID
 *
 * @return {string} A unique ID
 */
export const getUniqueID = clientID => {
	// Create a unique ID based on the block's clientId.
	const last7 = ( 'ugb-' + clientID.substring( clientID.length - 7 ) )
	const first7 = ( 'ugb-' + clientID.substring( 0, 7 ) )

	// Make sure we have a unique one.
	const lastExists = document.querySelectorAll( `.${ last7 }` ).length > 1
	return lastExists ? first7 : last7
}

/**
 * Gets Bootstrap class based on columns
 *
 */
export const getBootstrapClasses = ( col = false, smCol = false, mdCol = false, lgCol = false, xlCol = false ) => {
	const bootstrapClasses = [];
	col && bootstrapClasses.push( `col-${ 12/parseInt(col) }` );
	smCol && bootstrapClasses.push( `col-sm-${ 12/parseInt(smCol) }` );
	mdCol && bootstrapClasses.push( `col-md-${ 12/parseInt(mdCol) }` );
	lgCol && bootstrapClasses.push( `col-lg-${ 12/parseInt(lgCol) }` );
	xlCol && bootstrapClasses.push( `col-xl-${ 12/parseInt(xlCol) }` );
	return classnames( bootstrapClasses );
}

/**
 * Gets Slick items based on columns
 *
 */
export const getSlickItems = ( col = false, smCol = false, mdCol = false, lgCol = false, xlCol = false ) => {
	let items = 0;
	let itemsResponsive = [];

	if( col ) {
		itemsResponsive.push( { "breakpoint": 575, "settings": { "slidesToShow": parseInt(col) } } );
		items = parseInt(col);
	}

	if( smCol ) {
		itemsResponsive.push( { "breakpoint": 767, "settings": { "slidesToShow": parseInt(smCol) } } );
		items = parseInt(smCol);
	}

	if( mdCol ) {
		itemsResponsive.push( { "breakpoint": 991, "settings": { "slidesToShow": parseInt(mdCol) } } );
		items = parseInt(mdCol);
	}

	if( lgCol ) {
		itemsResponsive.push( { "breakpoint": 1199, "settings": { "slidesToShow": parseInt(lgCol) } } );
		items = parseInt(lgCol);
	}

	if( xlCol ) {
		items = parseInt(xlCol);
	}

	return { items, itemsResponsive };
}

