import { __ } from '@wordpress/i18n'
import { applyFilters } from '@wordpress/hooks'
import { compose } from '@wordpress/compose'
import { withSelect, withDispatch } from '@wordpress/data'
import { PanelBody, RangeControl, TextControl, TextareaControl } from '@wordpress/components'
import { PostTypeCheck, PostIDSelector, ImageControl } from '@frontgb/components'
import { Fragment } from '@wordpress/element'
import { range } from '@frontgb/util'
import { i18n } from 'frontgb'

let CustomerStoryPluginSidebar = (props) => {
    const { featuredLogo, setFeaturedLogo, additionalInformationCount, setAdditionalInformationCount, keyFeaturesCount, setKeyFeaturesCount, customStaticContentID, setCustomStaticContentID, additionalInformation, setAdditionalInformation, keyFeatures, setKeyFeatures } = props
    return(
        <Fragment>
            <PostTypeCheck name="customer_story">
                <PanelBody title={ __( 'General', i18n ) } initialOpen={ false }>
                    <ImageControl
                        label={ __( 'Featured Logo', i18n ) }
                        onRemove={ media => setFeaturedLogo( { id: '', url: '' } ) }
                        onChange={ media => setFeaturedLogo( { id: media.id, url: media.url } ) }
                        imageID={ featuredLogo.id && featuredLogo.url ? featuredLogo.id : '' }
                        imageURL={ featuredLogo.id && featuredLogo.url ? featuredLogo.url : '' }
                        allowedTypes={ [ 'image' ] }
                    />
                    <RangeControl
                        label={ __( 'Additional Information Count', i18n ) }
                        value={ additionalInformationCount }
                        onChange={ setAdditionalInformationCount }
                        min={ 1 }
                        max={ applyFilters( 'frontgb.customerStory.meta.additionalInformation.max.count', 25 ) }
                    />
                    <RangeControl
                        label={ __( 'Key Features Count', i18n ) }
                        value={ keyFeaturesCount }
                        onChange={ setKeyFeaturesCount }
                        min={ 1 }
                        max={ applyFilters( 'frontgb.customerStory.meta.keyFeatures.max.count', 25 ) }
                    />
                    <PostIDSelector
                        onPostSelect={ customStaticContentID => {
                            setCustomStaticContentID( customStaticContentID )
                        } }
                        posts={ customStaticContentID ? [ customStaticContentID ] : [] }
                        onChange={ customStaticContentIDs => {
                            if( customStaticContentIDs && customStaticContentIDs.length ) {
                                setCustomStaticContentID( customStaticContentIDs[0] )
                            } else {
                                setCustomStaticContentID( 0 )
                            }
                        } }
                        postType="mas_static_content"
                        limit="1"
                    />
                </PanelBody>
                { additionalInformationCount && (
                    <PanelBody title={ __( 'Additional Information', i18n ) } initialOpen={ false }>
                        { range( 1, additionalInformationCount + 1 ).map( i => {
                            return(
                                <Fragment key={ i }>
                                    { i > 1 ? <hr/> : '' }
                                    <h2>{ __( `Info ${ i }`, i18n ) }</h2>
                                    <TextControl
                                        label={ __( 'Label', i18n ) }
                                        value={ additionalInformation[ `label${ i }` ] }
                                        onChange={ label => setAdditionalInformation( { ...additionalInformation, [ `label${ i }` ]: label } ) }
                                    />
                                    <TextareaControl
                                        label={ __( 'Value', i18n ) }
                                        value={ additionalInformation[ `value${ i }` ] }
                                        onChange={ value => setAdditionalInformation( { ...additionalInformation, [ `value${ i }` ]: value } ) }
                                    />
                                </Fragment>
                            )
                        } ) }
                    </PanelBody>
                ) }
                { keyFeaturesCount && (
                    <PanelBody title={ __( 'Key Features', i18n ) } initialOpen={ false }>
                        { range( 1, keyFeaturesCount + 1 ).map( i => {
                            return(
                                <Fragment key={ i }>
                                    { i > 1 ? <hr/> : '' }
                                    <h2>{ __( `Feature ${ i }`, i18n ) }</h2>
                                    <ImageControl
                                        label={ __( 'Image', i18n ) }
                                        onRemove={ media => setKeyFeatures( { ...keyFeatures, [ `imageID${ i }` ]: '', [ `imageUrl${ i }` ]: '' } ) }
                                        onChange={ media => setKeyFeatures( { ...keyFeatures, [ `imageID${ i }` ]: media.id, [ `imageUrl${ i }` ]: media.url } ) }
                                        imageID={ keyFeatures[ `imageID${ i }` ] && keyFeatures[ `imageUrl${ i }` ] ? keyFeatures[ `imageID${ i }` ] : '' }
                                        imageURL={ keyFeatures[ `imageID${ i }` ] && keyFeatures[ `imageUrl${ i }` ] ? keyFeatures[ `imageUrl${ i }` ] : '' }
                                        allowedTypes={ [ 'image' ] }
                                    />
                                    <TextControl
                                        label={ __( 'Title', i18n ) }
                                        value={ keyFeatures[ `title${ i }` ] }
                                        onChange={ title => setKeyFeatures( { ...keyFeatures, [ `title${ i }` ]: title } ) }
                                    />
                                    <TextareaControl
                                        label={ __( 'Description', i18n ) }
                                        value={ keyFeatures[ `description${ i }` ] }
                                        onChange={ description => setKeyFeatures( { ...keyFeatures, [ `description${ i }` ]: description } ) }
                                    />
                                </Fragment>
                            )
                        } ) }
                    </PanelBody>
                ) }
            </PostTypeCheck>
        </Fragment>
    )
}

export default compose(
    withSelect( ( select ) => {
        const { getEditedPostAttribute } = select( 'core/editor' )
        const meta = getEditedPostAttribute( 'meta' ) || {}
        const { _featured_logo, _additional_information_count, _key_features_count, _custom_static_content_id, _additional_information, _key_features } = meta
        return {
            featuredLogo: _featured_logo ? JSON.parse( _featured_logo ) : {},
            additionalInformationCount: _additional_information_count ? _additional_information_count : 1,
            keyFeaturesCount: _key_features_count ? _key_features_count : 1,
            customStaticContentID: _custom_static_content_id ? _custom_static_content_id : 0,
            additionalInformation: _additional_information ? JSON.parse( _additional_information ) : {},
            keyFeatures: _key_features ? JSON.parse( _key_features ) : {},
        }
    } ),
    withDispatch( ( dispatch ) => ( {
        setFeaturedLogo( value ) {
            dispatch( 'core/editor' ).editPost( { meta: { _featured_logo: JSON.stringify( value ) } } )
        },
        setAdditionalInformationCount( value ) {
            dispatch( 'core/editor' ).editPost( { meta: { _additional_information_count: value } } )
        },
        setKeyFeaturesCount( value ) {
            dispatch( 'core/editor' ).editPost( { meta: { _key_features_count: value } } )
        },
        setCustomStaticContentID( value ) {
            dispatch( 'core/editor' ).editPost( { meta: { _custom_static_content_id: value } } )
        },
        setAdditionalInformation( value ) {
            dispatch( 'core/editor' ).editPost( { meta: { _additional_information: JSON.stringify( value ) } } )
        },
        setKeyFeatures( value ) {
            dispatch( 'core/editor' ).editPost( { meta: { _key_features: JSON.stringify( value ) } } )
        },
    } ) ),
)( CustomerStoryPluginSidebar )