import { BaseControl, Dashicon } from '@wordpress/components'
import { __ } from '@wordpress/i18n'
import { i18n } from 'frontgb'
import { Fragment } from '@wordpress/element'
import { MediaUpload } from '@wordpress/block-editor'
import SVGImageIcon from './images/image.svg'

function ImageControl( props ) {
	const {
		label,
		imageID,
		imageURL,
		onChange = ( { url, id } ) => {}, // eslint-disable-line no-unused-vars
		onRemove = () => {},
		allowedTypes = [ 'image' ],
		help,
		multiple,
		gallery,
	} = props

	const type = ! multiple && imageURL && imageURL.match( /(mp4|webm|ogg)/i ) ? 'video' : 'image';

	return (
		<BaseControl label={ label } help={ help } className="ugb-image-control">
			<MediaUpload
				onSelect={ onChange }
				allowedTypes={ allowedTypes }
				value={ imageID || imageURL }
				multiple={ multiple }
				gallery={gallery}
				addToGallery={gallery}
				render={ obj => {
					return (
						<Fragment>
							{ imageURL && 
								( multiple ? (
									(imageURL.map( ( url, key ) => {
										const itype = url && url.match( /(mp4|webm|ogg)/i ) ? 'video' : 'image';
										return (
											<div className="ugb-image-preview-wrapper" key={ key.toString() }>
												{ ! gallery && (
													<button className="ugb-image-preview-remove" onClick={ () => onRemove( key ) }><Dashicon icon="no" /></button>
												) }
												{ itype === 'image' && (
													<img
														className="ugb-image-preview"
														src={ url }
														onClick={ obj.open }
														onKeyDown={ event => {
															if ( event.keyCode === 13 ) {
																obj.open()
															}
														} }
														alt={ __( 'preview', i18n ) }
													/>
												) }
												{ itype === 'video' && (
													<video
														className="ugb-image-preview"
														autoPlay
														muted
														loop
														src={ url }
														onClick={ obj.open }
														onKeyDown={ event => {
															if ( event.keyCode === 13 ) {
																obj.open()
															}
														} }
													/>
												) }
											</div>
										)
									} ) )
								) : (
									<div className="ugb-image-preview-wrapper">
										{<button className="ugb-image-preview-remove" onClick={ onRemove }><Dashicon icon="no" /></button>}
										{ type === 'video' && (
											<video
												className="ugb-image-preview"
												autoPlay
												muted
												loop
												src={ imageURL }
												onClick={ obj.open }
												onKeyDown={ event => {
													if ( event.keyCode === 13 ) {
														obj.open()
													}
												} }
											/>
										) }
										{ type === 'image' && (
											/* eslint-disable-next-line jsx-a11y/no-noninteractive-element-interactions */
											<img
												className="ugb-image-preview"
												src={ imageURL }
												onClick={ obj.open }
												onKeyDown={ event => {
													if ( event.keyCode === 13 ) {
														obj.open()
													}
												} }
												alt={ __( 'preview', i18n ) }
											/>
										) }
									</div>
								) )
							}
							{ ( ( ! imageURL ) || ( multiple && imageURL && imageURL.length <= 0 ) ) && (
								<div
									className="ugb-placeholder"
									onClick={ obj.open }
									onKeyDown={ event => {
										if ( event.keyCode === 13 ) {
											obj.open()
										}
									} }
									role="button"
									tabIndex={ 0 }
								>
									<SVGImageIcon />
								</div>
							) }
						</Fragment>
					)
				} }
			/>
		</BaseControl>
	)
}

export default ImageControl