import { DesignControl, IconControl, PanelColorSettings, BackgroundSettings } from '@frontgb/components'
import { RangeControl, SelectControl, ToggleControl, BaseControl } from '@wordpress/components'
import { __ } from '@wordpress/i18n'
import { applyFilters } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import ImageDesignBasic from './images/basic.png'
import ImageDesignGhost from './images/ghost.png'
import ImageDesignLink from './images/link.png'
import ImageDesignPlain from './images/plain.png'
import { i18n } from 'frontgb'
import { URLInputControl } from '@frontgb/components'

function PanelButtonSettings( props ) {
	const {
		initialOpen=false,
		title=__( 'Button Settings', i18n ),
		enableButton,
		buttonBackground,
		buttonDesign,
		buttonColor,
		buttonSize,
		isWide,
		isWideSM,
		isDisable,
		isBlock,
		buttonBorderRadius,
		buttonIcon,
		isIconAfterText,
		isIconButton,
		enableTransition,
		onChangeEnableButton,
		onChangeButtonDesign,
		onChangeButtonBackground,
		onChangeButtonSize, // = () => {},
		onChangeWide,
		onChangeWideSM,
		onChangeDisable,
		onChangeBlock,
		onChangeButtonBorderRadius, // = () => {},
		onChangeButtonIcon, // = () => {},
		onChangeButtonIconAfterText,
		onChangeIconButton,
		onChangeEnableTransition,
		url,
		onChangeUrl,
		newTab,
		onChangeNewTab,
	} = props

	const colorSettings = []

	return (
		<Fragment>
			<PanelColorSettings
				initialOpen={ initialOpen }
				title={title}
				colorSettings={ colorSettings }
				className={ onChangeButtonDesign ? 'fgb-has-designs' : null }
				{ ...props }
			>
				{ onChangeEnableButton &&
				<ToggleControl
					label={ __( 'Enable Button', i18n ) }
					checked={ enableButton }
					onChange={ onChangeEnableButton }
				/>
				}
				{ onChangeButtonDesign &&
				<DesignControl
					label={ __( 'Design', i18n ) }
					selected={ buttonDesign }
					options={ [
						{ label: __( 'Default', i18n ), value: 'default', image: ImageDesignBasic },
						{ label: __( 'Outline', i18n ), value: 'outline', image: ImageDesignGhost },
						{ label: __( 'Soft', i18n ), value: 'soft', image: ImageDesignPlain },
						{ label: __( 'Text', i18n ), value: 'text', image: ImageDesignLink },
						...applyFilters( 'frontgb.button.edit.designs', [] ),
					] }
					onChange={ onChangeButtonDesign }
				/>
				}
				{ onChangeButtonBackground && 
				<BackgroundSettings 
					background={ buttonBackground }
					onChangeBackground={ onChangeButtonBackground }
					softBackground={ false }
					prefix=''
				/>
				}
				{ onChangeButtonSize &&
				<SelectControl
					label={ __( 'Size', i18n ) }
					value={ buttonSize }
					options={ [
						{ value: 'btn-xs', label: __( 'Extra Small', i18n ) },
						{ value: 'btn-sm', label: __( 'Small', i18n ) },
						{ value: 'default', label: __( 'Default', i18n ) },
						{ value: 'btn-lg', label: __( 'Large', i18n ) }
					] }
					onChange={ onChangeButtonSize }
				/>
				}
				{ onChangeWide &&
					<ToggleControl
						label={ __( 'Is Wide ?', i18n ) }
						checked={ isWide }
						onChange={ onChangeWide }
					/>
				}
				{ onChangeWideSM &&
					<ToggleControl
						label={ __( 'Is Wide SM ?', i18n ) }
						checked={ isWideSM }
						onChange={ onChangeWideSM }
					/>
				}
				{ onChangeDisable &&
					<ToggleControl
						label={ __( 'Is Disable Button ?', i18n ) }
						checked={ isDisable }
						onChange={ onChangeDisable }
					/>
				}
				{ onChangeBlock && 
					<ToggleControl
						label={ __( 'Is Block ?', i18n ) }
						checked={ isBlock }
						onChange={ onChangeBlock }
					/>
				}
				{ onChangeButtonBorderRadius &&
				<SelectControl
					label={ __( 'Border Radius', i18n ) }
					value={ buttonBorderRadius }
					options={ [
						{ value: 'rounded-0', label: __( 'Rounded 0', i18n ) },
						{ value: 'default', label: __( 'Default', i18n ) },
						{ value: 'btn-pill', label: __( 'Pill', i18n ) },
						{ value: 'rounded-circle', label: __( 'Circle', i18n ) }
					]}
					onChange={ onChangeButtonBorderRadius }
				/>
				}
				{ onChangeButtonIcon &&
					<IconControl
						label={ __( 'Icon', i18n ) }
						value={ buttonIcon }
						onChange={ onChangeButtonIcon }
					/>
				}
				{ onChangeButtonIconAfterText && 
					<ToggleControl
						label={ __( 'Is Icon After Text ?', i18n ) }
						checked={ isIconAfterText }
						onChange={ onChangeButtonIconAfterText }
					/>
				}
				{ onChangeIconButton && 
					<ToggleControl
						label={ __( 'Is Icon Button ?', i18n ) }
						checked={ isIconButton }
						onChange={ onChangeIconButton }
					/>
				}
				{ onChangeEnableTransition &&
					<ToggleControl
						label={ __( 'Enable Transition', i18n ) }
						checked={ enableTransition }
						onChange={ onChangeEnableTransition }
					/>
				}

				{ onChangeUrl && 
					<BaseControl label={ __( 'Button Link', i18n ) }>
						{ onChangeNewTab ? (
							<URLInputControl
								value={ url }
								onChange={ onChangeUrl }
								newTab={ newTab }
								onChangeNewTab={ onChangeNewTab }
							/>
						) : (
							<URLInputControl
								value={ url }
								onChange={ onChangeUrl }
							/>
						) }
					</BaseControl>
				}
				{ props.children }
				
			</PanelColorSettings>
		</Fragment>
	)
}

export default PanelButtonSettings
