import { __ } from '@wordpress/i18n'
import { compose } from '@wordpress/compose'
import { withSelect, withDispatch } from '@wordpress/data'
import { PanelBody, CheckboxControl, ToggleControl, SelectControl, TextControl } from '@wordpress/components'
import { PostTypeCheck, PostIDSelector } from '@frontgb/components'
import { Fragment } from '@wordpress/element'
import { i18n } from 'frontgb'

let PagePluginSidebar = (props) => {
    const { frontOptions, setFrontOptions } = props
    const { bodyClasses = '', disableContainer = false, contentClasses = '', hidePageHeader = false, isCustomHeader = false, headerStaticContentID = false, isCustomFooter = false, footerStaticContentID = false } = frontOptions

    return(
        <Fragment>
            <PostTypeCheck name="page">
                <PanelBody title={ __( 'General', i18n ) } initialOpen={ false }>
                    <TextControl
                        label={ __( 'Body Class', i18n ) }
                        value={ bodyClasses }
                        onChange={ bodyClasses => setFrontOptions( { ...frontOptions, bodyClasses: bodyClasses } ) }
                    />
                    <ToggleControl
                        label={ __( 'Disable Container ?', i18n ) }
                        checked={ disableContainer }
                        onChange={ disableContainer => setFrontOptions( { ...frontOptions, disableContainer } ) }
                    />
                    <TextControl
                        label={ __( 'Content Class', i18n ) }
                        value={ contentClasses }
                        onChange={ contentClasses => setFrontOptions( { ...frontOptions, contentClasses: contentClasses } ) }
                    />
                    <ToggleControl
                        label={ __( 'Hide Page Header', i18n ) }
                        checked={ hidePageHeader }
                        onChange={ hidePageHeader => setFrontOptions( { ...frontOptions, hidePageHeader } ) }
                    />
                </PanelBody>
                <PanelBody title={ __( 'Header', i18n ) } initialOpen={ false }>
                    <ToggleControl
                        label={ __( 'Enable Custom Header', i18n ) }
                        checked={ isCustomHeader }
                        onChange={ isCustomHeader => setFrontOptions( { ...frontOptions, isCustomHeader } ) }
                    />
                    { isCustomHeader && (
                        <PostIDSelector
                            onPostSelect={ headerStaticContentID => {
                                setFrontOptions( { ...frontOptions, headerStaticContentID } )
                            } }
                            posts={ headerStaticContentID ? [ headerStaticContentID ] : [] }
                            onChange={ headerStaticContentIDs => {
                                if( headerStaticContentIDs && headerStaticContentIDs.length ) {
                                    setFrontOptions( { ...frontOptions, headerStaticContentID: headerStaticContentIDs[0] } )
                                } else {
                                    setFrontOptions( { ...frontOptions, headerStaticContentID: false } )
                                }
                            } }
                            postType="mas_static_content"
                            limit="1"
                        />
                    ) }
                </PanelBody>
                <PanelBody title={ __( 'Footer', i18n ) } initialOpen={ false }>
                    <ToggleControl
                        label={ __( 'Enable Custom Footer', i18n ) }
                        checked={ isCustomFooter }
                        onChange={ isCustomFooter => setFrontOptions( { ...frontOptions, isCustomFooter } ) }
                    />
                    { isCustomFooter && (
                        <PostIDSelector
                            onPostSelect={ footerStaticContentID => {
                                setFrontOptions( { ...frontOptions, footerStaticContentID } )
                            } }
                            posts={ footerStaticContentID ? [ footerStaticContentID ] : [] }
                            onChange={ footerStaticContentIDs => {
                                if( footerStaticContentIDs && footerStaticContentIDs.length ) {
                                    setFrontOptions( { ...frontOptions, footerStaticContentID: footerStaticContentIDs[0] } )
                                } else {
                                    setFrontOptions( { ...frontOptions, footerStaticContentID: false } )
                                }
                            } }
                            postType="mas_static_content"
                            limit="1"
                        />
                    ) }
                </PanelBody>
            </PostTypeCheck>
        </Fragment>
    )
}

export default compose(
    withSelect( ( select ) => {
        const { getEditedPostAttribute } = select( 'core/editor' )
        const meta = getEditedPostAttribute( 'meta' ) || {}
        const { _front_options } = meta
        return {
            frontOptions: _front_options ? JSON.parse( _front_options ) : {},
        }
    } ),
    withDispatch( ( dispatch ) => ( {
        setFrontOptions( value ) {
            dispatch( 'core/editor' ).editPost( { meta: { _front_options: JSON.stringify( value ) } } )
        },
    } ) ),
)( PagePluginSidebar )