import { __ } from '@wordpress/i18n'
import { Component } from '@wordpress/element'
import classnames from 'classnames'
import { applyFilters } from '@wordpress/hooks'
import $ from 'jquery'

class SeparatorEdit extends Component {

    componentDidMount() {
        this.editorScriptInit();
    }

    componentDidUpdate() {
        this.editorScriptInit();
    }

    editorScriptInit() {
        if( $(this.SVGIngector).length ) {
            $.HSCore.components.HSSVGIngector.init(this.SVGIngector);
        }
    }

    render() {
        const props = { ...this.props }
        const { 
            className,
            alt = '',
            svg,
            dataParent,
            dataImgPath = false,
        } = props

        const mainClasses = classnames( [
            className,    
        ], applyFilters( 'frontgb.separator.mainclasses', {}, svg, props ) )

        return (
            <figure className={mainClasses}>
                { dataImgPath ? (
                    <img ref={el => this.SVGIngector = el} className="js-svg-injector" src= { svg } alt={ alt } data-parent={ dataParent } data-img-paths={ dataImgPath } />
                ) : (
                    <img ref={el => this.SVGIngector = el} className="js-svg-injector" src= { svg } alt={ alt } data-parent={ dataParent } />
                ) }
            </figure>
        )
    }
}

export default SeparatorEdit