import { applyFilters } from '@wordpress/hooks'
import classnames from 'classnames'
import { PanelSocialNetworksSettings, SVGInjector  } from '@frontgb/components'
import { Fragment } from '@wordpress/element'
import { __ } from '@wordpress/i18n'
import { range } from '@frontgb/util'
import { getIconClassName, getIconPath } from '@frontgb/components/icon-control'

const SocialNetworkEdit = props => {
    const {
        className,
        enableTransparent=true,
        enableCircle=false,
        iconBackground,
        iconCustomBackground,
        socialNetworkLink,
        socialNetworkIcon,
        design='default',
    } = props 

    let transparentClass ='';

    if ( enableTransparent === true ) {
            transparentClass =  classnames( 'btn-bg-transparent' ) 
    }

    let iconClass = '';

    if ( enableCircle === true ) {
            iconClass =  classnames( 'rounded-circle' ) 
    }

    let iconComp = false;

    if( socialNetworkIcon && socialNetworkIcon.startsWith("fgb") ) { 

        const iconPath = getIconPath( socialNetworkIcon )
        const figClasses = classnames( [ 
            'mx-auto mb-3 w-100',
        ] )

        iconComp = (
            <SVGInjector
                classNameFig={ figClasses }
                className="js-svg-injector"
                src={ iconPath }
                alt="SVG"
            />
        )
    } 
    else {
        const snIconClasses = getIconClassName( socialNetworkIcon )

        const iconClasses = classnames( [ 
            snIconClasses,
            'btn-icon__inner',
        ])

        iconComp = <span className={ iconClasses }></span>
    }

    const mainClasses = classnames( [
        'btn',
        'btn-sm', 
        'btn-icon',
        transparentClass,
        iconClass,    
    ], {
        [`btn-${ iconBackground }`]: design === 'default',
        [`btn-${ design }-${ iconBackground }`]: design !== 'default',
    } )

    const mainStyle = {backgroundColor: iconCustomBackground && iconCustomBackground}

    return (
        <a  className={ mainClasses } href= {socialNetworkLink} style={ mainStyle }> 
            { iconComp }
        </a>  
    )
}

export default SocialNetworkEdit