import { Component, Children } from '@wordpress/element'
import $ from 'jquery'
import { isRTL } from 'frontgb'

class SlickEditor extends Component {

    componentWillUpdate( newProps ) {
        if ( Children.count( this.props.children ) !== Children.count( newProps.children ) ) {
            this.editorScriptRemove()
        } else if( this.props.reInitOnEdit && Children.count( this.props.children ) <= 1 && this.props.children !== newProps.children ) {
            this.editorScriptRemove()
        }
    }

    componentDidMount() {
        this.editorScriptInit()
    }

    componentDidUpdate( prevProps ) {
        if ( Children.count( this.props.children ) !== Children.count( prevProps.children ) ) {
            this.editorScriptInit()
            this.editorScriptReInit()
        } else if( this.props.reInitOnEdit && Children.count( this.props.children ) <= 1 && this.props.children !== prevProps.children ) {
            this.editorScriptInit()
            this.editorScriptReInit()
        } else if( this.props.children !== prevProps.children ) {
            this.editorScriptReInit()
        }
    }

    editorScriptInit() {
        if( $(this.slick).length ) {
            $.HSCore.components.HSSlickCarousel.init(this.slick)
            $(this.slick).slick('slickSetOption', 'rtl', isRTL && true, true )
            
        }
    }

    editorScriptRemove() {
        if( $(this.slick).length  ) {
            $(this.slick).slick('unslick')
        }
    }

    editorScriptReInit() {
        if( $(this.slick).length  ) {
            $(this.slick).slick('slickSetOption', 'slidesToShow', this.props['data-slides-show'], true )
            $(this.slick).slick('slickSetOption', 'rtl', isRTL && true, true )
        }
    }



    render() {
        const {
            children,
        } = this.props
        

        return (
            <div ref={el => this.slick = el} { ...this.props }>
                { children }
            </div>
        )
    }
}

export default SlickEditor