import { BaseControl, Button, ButtonGroup, RadioControl, Dropdown, ColorPicker, Dashicon, ToggleControl } from '@wordpress/components'
import { ColorPaletteControl, PanelColorSettings } from '@frontgb/components'
import { __ } from '@wordpress/i18n'
import { applyFilters } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import ImageControl from '../image-control'
import { i18n } from 'frontgb'

import { setAttributes } from 'lodash';

function BackgroundSettings( props ) {
    const {
        backgroundType = '',
        onChangeBackgroundType,
        background,
        onChangeBackground,
        onRemoveBackground,
        backgroundColor,
        onChangeBackgroundColor,
        backgroundImageURL,
        onChangeBackgroundImage,
        onRemoveBackgroundImage,
        softBackground = true,
        prefix = 'bg-',
        backgroundLabel = "Background Color",
        backgroundImageLabel = "Background Image",
        disableAlpha = true,
    } = props

    const customColorPickerLabel = __( 'Custom color picker', i18n );
    
    return (
        <div className = "components-base-control frontgb-radiobutton-bg"> 
            <Fragment>
                { applyFilters( 'frontgb.background-settings.before', null, props ) }
                { onChangeBackgroundType  &&
                <BaseControl
                    label={ __( 'Background Type', i18n ) }
                >
                    <ButtonGroup
                        aria-label={ __( 'Image Size', i18n ) }
                    >
                        { onChangeBackground &&
                        <Button
                            isPrimary={ backgroundType === '' }
                            isDefault={ backgroundType !== '' }
                            onClick={ () => {
                                onChangeBackgroundType( '' )
                            } }
                        >
                            { __( 'Single', i18n ) }
                        </Button>
                        }
                        { onChangeBackground &&
                        <Button
                            isPrimary={ backgroundType === 'gradient' }
                            isDefault={ backgroundType !== 'gradient' }
                            onClick={ () => {
                                onChangeBackgroundType( 'gradient' )
                            } }
                        >
                            { __( 'Gradient', i18n ) }
                        </Button>
                        }
                        { ( onChangeBackgroundImage && onChangeBackground ) &&
                        <Button
                            isPrimary={ backgroundType === 'image' }
                            isDefault={ backgroundType !== 'image' }
                            onClick={ () => {
                                onChangeBackgroundType( 'image' )
                            } }
                        >
                            { __( 'Image', i18n ) }
                        </Button>
                        }
                    </ButtonGroup>
                </BaseControl>
                }
                { ( onChangeBackground && backgroundType === '' ) &&
                    <RadioControl
                        label={ backgroundLabel }
                        selected={ background }
                        options={[
                            { label: __( 'Primary', i18n ), value: `${prefix}primary` },
                            { label: __( 'Secondary', i18n ), value: `${prefix}secondary` },
                            { label: __( 'Success', i18n ), value: `${prefix}success` },
                            { label: __( 'Danger', i18n ), value: `${prefix}danger` },
                            { label: __( 'Warning', i18n ), value: `${prefix}warning` },
                            { label: __( 'Info', i18n ), value: `${prefix}info` },
                            { label: __( 'Dark', i18n ), value: `${prefix}dark` },
                            { label: __( 'Light', i18n ), value: `${prefix}light` },
                            { label: __( 'Indigo', i18n ), value: `${prefix}indigo` },
                            { label: __( 'White', i18n ), value: `${prefix}white` },
                            ...( softBackground ? [
                                { label: __( 'Soft Primary', i18n ), value: `${prefix}soft-primary` },
                                { label: __( 'Soft Secondary', i18n ), value: `${prefix}soft-secondary` },
                                { label: __( 'Soft Success', i18n ), value: `${prefix}soft-success` },
                                { label: __( 'Soft Danger', i18n ), value: `${prefix}soft-danger` },
                                { label: __( 'Soft Warning', i18n ), value: `${prefix}soft-warning` },
                                { label: __( 'Soft Warning', i18n ), value: `${prefix}soft-indigo` },
                                { label: __( 'Soft Info', i18n ), value: `${prefix}soft-info` },
                                { label: __( 'Soft Dark', i18n ), value: `${prefix}soft-dark` }
                            ] : [] ),
                        ]}
                        onChange={ onChangeBackground }
                    />
                }
                {   ( onChangeBackground && backgroundType === 'gradient' ) &&
                    <RadioControl
                        label={ backgroundLabel }
                        selected={ background }
                        options={ applyFilters( 'frontgb.background-settings.edit.designs', [
                            { label: 'Gradient Half Primary v1', value: 'gradient-half-primary-v1' },
                            { label: 'Gradient Half Primary v4', value: 'gradient-half-primary-v4' },
                            { label: 'Gradient Half Primary v5', value: 'gradient-half-primary-v5' },
                            { label: 'Gradient Half Info v1', value: 'gradient-half-info-v1' },
                            { label: 'Gradient Half Warning v1', value: 'gradient-half-warning-v1' },
                            { label: 'Gradient Half Primary v2', value: 'gradient-half-primary-v2' },
                            { label: 'Gradient Half Warning v2', value: 'gradient-half-warning-v2' },
                            { label: 'Gradient Half Warning v3', value: 'gradient-half-warning-v3' },
                            { label: 'Gradient Half Primary Body v1', value: 'gradient-half-primary-body-v1' },
                            { label: 'Gradient Half Primary v3', value: 'gradient-half-primary-v3' },
                        ] ) }
                        onChange={ onChangeBackground }
                    />
                }
                { ( ( backgroundType === '' || backgroundType === 'gradient' ) && onChangeBackgroundColor ) &&
                <div className="components-color-palette__custom-clear-wrapper clear-button">
                    <Dropdown
                        className="components-color-palette__custom-color"
                        contentClassName="components-color-palette__picker"
                        renderToggle={ ( { isOpen, onToggle } ) => (
                            <Button
                                aria-expanded={ isOpen }
                                onClick={ onToggle }
                                aria-label={ customColorPickerLabel }
                                isLink
                            >
                                { __( 'Custom Color', i18n ) }
                            </Button>
                        ) }
                        renderContent={ () => (
                            <ColorPicker 
                                color = { backgroundColor }
                                onChangeComplete = { onChangeBackgroundColor }
                                disableAlpha={disableAlpha}
                            />
                        ) }
                    />
                    <Button
                        className="components-color-palette__clear"
                        type="button"
                        onClick= { onRemoveBackground }
                        isSmall
                        isDefault
                    >
                        { __( 'Clear', i18n ) }
                    </Button>
                </div>
                }
                { ( ( onChangeBackgroundImage && backgroundType === 'image' ) || ( onChangeBackgroundImage && onChangeBackground === undefined ) ) &&
                    <ImageControl
                        label={ backgroundImageLabel }
                        onRemove={ media => {
                            if ( onChangeBackgroundType ) {
                                onChangeBackgroundType( '' )
                            }
                            onRemoveBackgroundImage( media )
                        } }
                        onChange={ media => {
                            if ( onChangeBackgroundType ) {
                                onChangeBackgroundType( media.url.match( /(mp4|webm|ogg)/i ) ? 'video' : '' )
                            }
                            onChangeBackgroundImage( media )
                        } }
                        imageURL={ backgroundImageURL }
                        allowedTypes={ onChangeBackgroundType ? [ 'image', 'video' ] : [ 'image' ] }
                    />
                }
                { applyFilters( 'frontgb.background-settings.after', null, props ) }
            </Fragment>
        </div>
    )
}

export default BackgroundSettings