import { BaseControl, Button, ButtonGroup, Dropdown, RangeControl, SelectControl } from '@wordpress/components'
import { __ } from '@wordpress/i18n'
import { applyFilters } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { setAttributes } from 'lodash';
import { i18n } from 'frontgb'


function TypographySettings( props ) {
    const {
        fontSize = 'h4',
        customFontSize,
        onChangeFontSize,
        onChangeCustomFontSize,
        onRemoveFontSize,
        label = 'Font Size',
        fontWeightLabel = 'Font Weight',
        isButton = false,
        isDisplayFont = false,
        fontWeight,
        onChangeFontWeight,
        customFontWeight,
        onChangeCustomFontWeight,
        onRemoveFontWeight,
    } = props
    
    return (
        <div className = "components-base-control frontgb-fontszie-picker"> 
            <BaseControl label={ label } >
                <SelectControl
                    value={ fontSize }
                    options={ isButton === true ?
                        [
                            { label: __( 'Medium', i18n ), value: 'btn-sm' },
                            { label: __( 'Small', i18n ), value: 'btn-xs' },
                            { label: __( 'Large', i18n ), value: 'btn-lg' },
                        ] :
                        [
                            { label: __( 'Xtra Large', i18n ), value: 'h1' },
                            { label: __( 'Large', i18n ), value: 'h2' },
                            { label: __( 'Meduim', i18n ), value: 'h3' },
                            { label: __( 'Normal', i18n ), value: 'h4' },
                            { label: __( 'Small', i18n ), value: 'h5' },
                            { label: __( 'Xtra Small', i18n ), value: 'h6' },
                            { label: __( 'Lead', i18n ), value: 'lead' },
                            ...( isDisplayFont === true ? [
                                { label: __( 'Display 1', i18n ), value: 'display-1' },
                                { label: __( 'Display 2', i18n ), value: 'display-2' },
                                { label: __( 'Display 3', i18n ), value: 'display-3' },
                                { label: __( 'Display 4', i18n ), value: 'display-4' },
                            ] : '' )
                        ] 
                    }
                    onChange={ onChangeFontSize }
                />
                <RangeControl
                    value={ customFontSize }
                    onChange={ onChangeCustomFontSize }
                    min={ 1 }
                    max={ 200 }
                />
                <Button
                    className="components-fontsize__clear"
                    type="button"
                    onClick= { onRemoveFontSize }
                    isSmall
                    isDefault
                >
                    { __( 'Reset' ) }
                </Button>
            </BaseControl>
            <BaseControl label={ fontWeightLabel } >
                <SelectControl
                    value={ fontWeight }
                    options={ [
                            { label: __('Font Bold', i18n ), value: 'font-weight-bold' },
                            { label: __('Font Semi Bold', i18n ), value: 'font-weight-semi-bold' },
                            { label: __('Font Meduim', i18n ), value: 'font-weight-medium' },
                            { label: __('Font Normal', i18n ), value: 'font-weight-normal' },
                            { label: __('Font Light', i18n ), value: 'font-weight-light' },
                            { label: __('Font Italic', i18n ), value: 'font-italic' },
                        ] 
                    }
                    onChange={ onChangeFontWeight }
                />
                <RangeControl
                    value={ customFontWeight }
                    onChange={ onChangeCustomFontWeight }
                    step={ 100 }
                    min={ 100 }
                    max={ 900 }
                />
                <Button
                    className="components-fontsize__clear"
                    type="button"
                    onClick= { onRemoveFontWeight }
                    isSmall
                    isDefault
                >
                    { __( 'Reset' ) }
                </Button>
            </BaseControl>
        </div>
    )
}

export default TypographySettings