import { __ } from '@wordpress/i18n'
import { compose } from '@wordpress/compose'
import { withSelect, withDispatch } from '@wordpress/data'
import { PostTypeSupportCheck } from '@wordpress/editor'
import { CheckboxControl } from '@wordpress/components'
import { PostTypeCheck } from '@frontgb/components'
import { i18n } from 'frontgb'

let DocsPluginPostStatusInfo = (props) => {
    const { featured, setFeatured } = props
    return(
        <PostTypeCheck name="docs">
            <PostTypeSupportCheck supportKeys="custom-fields">
                <CheckboxControl
                    label={ __( 'Featured', i18n ) }
                    checked={ featured }
                    onChange={ setFeatured }
                />
            </PostTypeSupportCheck>
        </PostTypeCheck>
    )
}

export default compose(
    withSelect( ( select ) => {
        const { getEditedPostAttribute } = select( 'core/editor' )
        const meta = getEditedPostAttribute( 'meta' ) || {}
        const { _featured = false } = meta
        return {
            featured: _featured,
        }
    } ),
    withDispatch( ( dispatch ) => ( {
        setFeatured( value ) {
            dispatch( 'core/editor' ).editPost( { meta: { _featured: value } } )
        },
    } ) ),
)( DocsPluginPostStatusInfo )